import { Link } from 'react-router-dom';
import { useState, useEffect, useContext, useRef } from 'react';
import { getNavbarData } from '../../../libs/home'
import { signOut } from '../../../libs/auth'
import { useQuery } from 'react-query'
import { MakeSlug } from '../../common/make-slug'
import {useParams, useHistory, useLocation} from 'react-router-dom'
import {getNotifications, readNotification} from '../../../libs/question'
import {Helmet} from 'react-helmet-async'
import {AuthContext} from '../../../context/AuthContext';
import { imageUrl } from '../../../config/config';
import { HashLink } from 'react-router-hash-link';
import TawkTo from '../../common/tawkto';

export default function DashboardNavbar({...props}){  
    const history = useHistory();
    const params =  useParams();
    const [ showNotification, setShowNotification ] = useState(false);
    const [ showDropdown, setShowDropdown ] = useState(false);
    const [ classname, setClassname ] = useState('');
    const [showMenu,setShowMenu] = useState(false);
    const [showAMenu,setShowAMenu] = useState(false);
    const {state, dispatch, menus:data} = useContext(AuthContext);
    const session = state.isLoggedIn;
    const [list, setList] = useState(0);

    /////
    const myRef = useRef();
    const profileRef = useRef();
    const handleClickOutside = e => {
        if (!myRef.current.contains(e.target)) {
          //hideNotification()
          setShowNotification(false);
        }
        if (!profileRef.current.contains(e.target)) {
          setShowDropdown(false);
        }
    };
    useEffect(() => {
        document.addEventListener("mousedown", handleClickOutside);
        return () => document.removeEventListener("mousedown", handleClickOutside);
    });
    /////


    async function SignOut () {
        const data = await signOut()
        if(data){
            dispatch({type: 'LOGOUT'})
            history.push('/')
        }
    }
    const openNotification = () => {
        setShowNotification(true);
        setShowDropdown(false);
        setShowAMenu(false);
        setShowMenu(false);
        setClassname('show');
    }

    const openDropdown = () => {
        setShowNotification(false);
        setShowDropdown(true);
        setShowAMenu(false);
        setShowMenu(false);
        setClassname('show');   
    }

    const openMenu = ()=>{
        setShowMenu(true);
        setShowAMenu(false);
        setShowNotification(false);
        setShowDropdown(false);
        setClassname('show');
    }

    const openMenuA = ()=>{
        setShowAMenu(true);
        setShowMenu(false);
        setShowNotification(false);
        setShowDropdown(false);
        setClassname('show');
    }

    const hideMenuA = ()=>{
        setShowAMenu(false);
        setClassname('show');
    }

    const hideDropdown = ()=>{
        setShowDropdown(false);
        setClassname('show');
    }

    const hideNotification = ()=>{
        setShowNotification(false);
        setClassname('show');
    }
  
    const hideMenu = ()=>{
        setShowMenu(false);
        setClassname('show');
    }

    const handleClick =()=>{
        hideMenu()
    }

    const openSideMenu =()=>{
        if(document.body.classList.contains('overlay-open')){
            document.querySelector("body").classList.remove("overlay-open")
        }else{
            document.querySelector("body").classList.add("overlay-open")
        }
    }

    const markAsRead = async(id, type, link, data_id) => {
        const res = await readNotification(id);
        let url = "";
        if(!res.error){
            if(type == "ASK50"){
                url = link + "#ASK50" + data_id;
            }else{
                url = link
                history.push(url);
            }
        }
    }
    const [subMenu, setSubMenu] = useState('');
    const openSubMenu = (key) => {
        setList(key)
        setSubMenu('show');
    }

    const isRead = false;
    
    //  const { data, isLoading } = useQuery('menus', getNavbarData,{ staleTime:Infinity})
    const { data: notifications, isLoading:notificationsIsLoading, error:notificationsError } = useQuery([`notifications-${isRead}`], () => getNotifications({user_Id : state._id, type: 'QA'}, isRead),{ staleTime : Infinity, enabled : !!session })
    
    return( <><TawkTo/>
            <nav className="navbar navbar-expand-lg navbar-light navbar-dashboad  navbar_dashboard1 p-l-5 p-r-5">
                <ul className="nav navbar-nav navbar-left nav_left1 mr-auto">
                    <li>
                        <div className="navbar-header">
                            <a href="#" className="bars" onClick={openSideMenu}></a>
                            <Link to="/" className="navbar-brand"><span className="m-l-60"><img src="/images/logo.png" className="img-fluid" alt="logo" width="100"/></span></Link>
                        </div>
                    </li>      
                </ul>
   
                <ul className="nav navbar-nav navbar-light  nav_right1 solu_nav ml-auto">
                    <li className="nav-item dp_n">
                        <HashLink to="/q-and-a#subjects" className="nav-link">Q and A
                        </HashLink>
                    </li>
                    <li className={`nav-item dp_n  ${classname}`} onMouseEnter={()=>{openMenu()}}>
                        <HashLink to="/textbook-solutions-manuals#solution-manuals" className="nav-link dropdown-toggle" id="dropdown01" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">Solutions Manual  </HashLink>
                        {showMenu &&
                        <ul className={`dropdown-menu dropdownmenu_main ${classname}`} aria-labelledby="navbarDropdown" onMouseLeave={()=>hideMenu()}>
                                {data && data.map((item,key)=>{
                                    return(<li className="dropright" key={key}>
                                        <Link className={"dropdown-item " + (list === key ? 'active' : '')} data-toggle="dropdown" to={`/textbook-solutions-manuals/${MakeSlug(item.subject)}`} onMouseEnter={()=>{openSubMenu(key)}}>
                                            <h6><img src={`/images/nav-icons/${MakeSlug(item.subject)}.png`} className="img-fluid" alt=""/> {item.subject} <i className="fa fa-angle-right"></i></h6>
                                        </Link> 
                                        <div className={"dropdown-menu dropdown_main2 " + (list === key ? 'show' : '')}> 
                                            {item.sub_subject.map((it,key)=>{
                                                return(
                                                    <HashLink to={`/textbook-solutions-manuals/${MakeSlug(item.subject)+'/'+MakeSlug(it.sub_subject)}`} key={key} className="dropdown-item" onClick={handleClick}><i className="fa fa-circle"></i> {it.sub_subject}</HashLink>
                                                )
                                            })}
                                        </div>
                                    </li>)
                                })}
                        </ul>}
                    </li>
                    <li className="nav-item dropdown_reltv dp_n" onMouseEnter={()=>{openMenuA()}}>
                        <Link to="/writing-help" className="nav-link dropdown-toggle" id="navbarDropdown" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                        Writing Help
                        </Link>
                        {showAMenu && 
                        <div className={`dropdown-menu sm-menu ${classname}`} aria-labelledby="navbarDropdown" onMouseLeave={()=>hideMenuA()}>
                            <Link to="/writing-help/online-assignment-help" className="dropdown-item"><img src="/images/nav-icons/online-assignment-help.png" className="img-fluid" alt=""/> Assignment Help </Link>
                        </div>}
                    </li>
                    <li className={`nav-item dmenu float-right pt_sty dropdown  ${classname}`} ref={profileRef} onMouseOver={openDropdown}>
                        <a className="nav-link dropdown-toggle" href="#" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                            <span className="my_pics_img m-r-60 mt-0">
                                <div className="navbar-default-profile-name" style={{display:(props.data && props.data.img ? 'none' : 'block')}}>
                                {props.data && props.data.Name ? props.data.Name.substring(0,1).toUpperCase() : '...'}</div>

                                {props.data && props.data.img && <img src={props.data.img.includes('http') ? props.data.img : imageUrl + props.data.img}
                                className="img-fluid" alt="User"/>}
                            </span>
                            </a>
                        {showDropdown && <><div className={`dropdown-menu sm-menu ${classname}`} aria-labelledby="navbarDropdown" onMouseLeave={()=>{hideDropdown()}}>
                            <Link to="/dashboard" className="dropdown-item" href="#" onClick={()=>setShowDropdown(false)}> Dashboard</Link>
                            <Link to="/user/my-orders" className="dropdown-item" href="#" onClick={()=>setShowDropdown(false)}> My Orders</Link>
                            <Link to="/user/my-profile" className="dropdown-item" href="#" onClick={()=>setShowDropdown(false)}> My Profile</Link>
                            <a className="dropdown-item" href="#" onClick={SignOut}><i className="fas fa-sign-out-alt"></i> Logout </a>
                        </div></>}
                    </li>

                    <div className="navbar_search" style={{display:"none"}}>
                        <form className="search-form">
                            <div className="input-group ml-0 pt-0">
                                <input type="text" className="form_search_control" placeholder="Search ISBN, textbook name or homework question here"/>
                                <div className="input-group-append">
                                    <span className="input-group-text"><i className="fa fa-search"></i></span>
                                </div>
                            </div>
                        </form><i className="fa fa-close close_btn_top"></i> 
                    </div>
                    
                    <li className={`dropdown float-right pt_sty bell_noti ${classname}`} ref={myRef}>
                        <a href="#" className="dropdown-toggle" data-toggle="dropdown" role="button" onMouseOver={()=>{openNotification()}}>
                            <i className="fa fa-bell zmdi zmdi-notifications"></i> 
                        </a>
                        <div className="notifica_numbr">
                            <span>{notifications && notifications?.data.length}</span>
                        </div>
                        {showNotification &&
                        <ul className={`dropdown-menu pullDown ${classname}`} onMouseLeave={()=>{hideNotification()}} >
                            <li className="body">
                                <ul className="menu list-unstyled notification_scroll">
                                    {notifications && notifications.data.map((item,key)=>{
                                         var date = new Date(item.created_at);
                                        return(
                                            <li key={key} onClick={()=>{markAsRead(item._id,item.type,item.link, item.data_Id)}}>
                                               
                                                <a href="#">
                                                        <div className="media">
                                                            <img className="media-object" src="/images/pic2.png" alt=""/>
                                                            <div className="media-body">
                                                            <span className="name">{item.type} <span className="time"></span></span>
                                                            <span className="message"><span dangerouslySetInnerHTML={{__html: item.info}}></span></span>
                                                            <span style={{fontSize : "10px"}}>{date.toLocaleString()}</span>                                    
                                                            </div>
                                                        </div>
                                                </a>
                                            </li>
                                        )
                                    })}
                                </ul>
                            </li>
                            <Link to="/user/notifications"><li className="footer" href="#" onClick={()=>setShowNotification(false)}>View All </li></Link>
                        </ul>}
                    </li>
                    <li className="float-right search_btn_top">
                    </li> 
                </ul>
            </nav>

            {/* <div className="chat-launcher">
            </div> */}

            <div className="chat-wrapper">
                <div className="card">
                    <div className="header">
                        <ul className="list-unstyled team-info margin-0">
                            <li className="m-r-15">
                                <h2> Alexander</h2>
                            </li>
                        </ul>
                    </div>
                    <div className="body">
                        <div className="chat-widget">
                            <ul className="chat-scroll-list clearfix">
                                <li className="left float-left">
                                    <img src="/images/pic1.png" className="rounded-circle" alt=""/>
                                    <div className="chat-info"> <a className="name" href="#">Elizabeth</a> <span className="datetime">6:25</span> <span className="message">Hi, Alexander,<br/> John <br/> What are you doing?</span></div>
                                </li>
                                <li className="right">
                                    <div className="chat-info"><span className="datetime">6:15</span> <span className="message">Hi, Alexander<br/> How are you!</span> </div>
                                </li>
                                <li className="right">
                                    <div className="chat-info"><span className="datetime">6:16</span> <span className="message">There are many variations of passages of Lorem Ipsum available</span> </div>
                                </li>
                                <li className="left float-left">
                                    <img src="/images/pic1.png" className="rounded-circle" alt=""/>
                                    <div className="chat-info"> <a className="name" href="#">Elizabeth</a> <span className="datetime">6:25</span> <span className="message">Hi, Alexander,<br/> John <br/> What are you doing?</span> </div>
                                </li>
                                <li className="left float-left">
                                    <img src="/images/pic2.png" className="rounded-circle" alt=""/>
                                    <div className="chat-info"> <a className="name" href="#">Michael</a> <span className="datetime">6:28</span> <span className="message">I would love to join the team.</span> </div>
                                </li>
                                <li className="right">
                                    <div className="chat-info"><span className="datetime">7:02</span> <span className="message">Hello, <br/>Michael</span> </div>
                                </li>
                            </ul>
                        </div>
                        <div className="input-group p-t-15">
                            <input type="text" className="form-control" placeholder="Enter text here..."/>
                            <span className="input-group-addon"><i className="zmdi zmdi-mail-send"></i></span>
                        </div>
                    </div>
                </div>
            </div>
	  </>
    )
}