import { Link } from "react-router-dom";
import { useContext } from "react";
import { AuthContext } from "../../../context/AuthContext";

export default function StepByStep() {
  const { selectedCon } = useContext(AuthContext);
  const content = selectedCon && selectedCon.content ? selectedCon.content : "";
  return (
    <section className="section typical_answer mt-5 mb-5">
      <div className="container">
        <div className="row">
          <div className="col-md-12 mb-5 text-center">
            <div className="Content_Covered_title pb-3">
              <h2>
                {content && content.question && content.question.heading
                  ? content.question.heading
                  : "Do you want to see how step-by-step textbook solution looks like"}
              </h2>
            </div>
          </div>
          <div className="col-md-6 pr-5 border_right">
            <div className="typical_text">
              <h4 className="pb-2">
                Question <span>posted by Student</span>
              </h4>
              <p>
                {content && content.question && content.question.content
                  ? content.question.content
                  : "“Graphs are really useful in Physics and the following pages show you the shapes of some of the most important graphs. (c, m and k are constants. Not necessarily the same value for each graph.) Graphical solutions to equations”"}
              </p>
            </div>
          </div>
          <div className="col-md-6 typical_text typical_text2">
            <div className="row">
              <div className="col-md-12">
                {content && content.answer && content.answer.heading ? (
                  <h4 className="pb-2 pl-4">{content.answer.heading}</h4>
                ) : (
                  <h4 className="pb-2 pl-4">
                    Answer <span>by our PHD Expert</span>
                  </h4>
                )}
              </div>
              {content && content.answer && content.answer.content ? (
                content.answer.content
              ) : (
                <>
                  <div className="col-md-5 graf1 pl-0">
                    <span>
                      <img
                        src="/images/physics-solve.png"
                        className="img-fluid"
                        alt=""
                      />
                    </span>
                  </div>
                  <div className="col-md-7 pl-0">
                    <p>
                      {" "}
                      The horizontal acceleration and time of flight being
                      known, the
                    </p>
                    <span>
                      <img
                        src="/images/answer_expert.jpg"
                        className="img-fluid"
                        alt=""
                      />
                    </span>
                    <p>
                      Next we shall consider a new problem:{" "}
                      <span className="red_text">
                        a body is thrown at an angle to an inclined plane which
                        makes the angle with the horizontal.
                      </span>{" "}
                    </p>
                  </div>
                </>
              )}
            </div>
          </div>
          <div className="col-md-12 btn1 text-center mt-4">
            <Link to="/paynow" className="pl-5 pr-5 text-black">
              Try Crazyforstudy
            </Link>
          </div>
        </div>
      </div>
    </section>
  );
}
